$mobile-breakpoint: 900px;

.CsoDirectory {
  @include clearfix;
}

.CsoDirectory-itemWrap {
  padding-top: 20px;

  @media screen and (min-width: $mobile-breakpoint) {
    width: 50%;
    float: left;
    padding: 20px 20px 0;

    &:nth-of-type(odd) {
      padding: 20px 20px 20px 0;
    }

    &:nth-of-type(even) {
      padding: 20px 0 20px 20px;
    }
  }
}

.CsoDirectory-item {
  position: relative;
  background-color: white;
  display: block;
  text-decoration: none;
  color: #606060;
  box-shadow:
    0 1px 3px 0 rgba(0, 0, 0, 0.2),
    0 1px 1px 0 rgba(0, 0, 0, 0.14),
    0 2px 1px -1px rgba(0, 0, 0, 0.12);
  border-radius: 3px;

  @media screen and (min-width: $mobile-breakpoint) {
    max-height: 230px;
  }
}

.CsoDirectory-box {
  list-style: none;
  padding: 15px 25px;

  &.is-dark {
    font-size: 13px;
    background-color: #e6e6e6;
  }
}

.CsoDirectory-title {
  display: block;
  font-size: 16px;
  margin: 0;
  text-decoration: underline;
  padding-bottom: 5px;

  @media screen and (min-width: $mobile-breakpoint) {
    max-height: 2.5em;
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;
  }
}

.CsoDirectory-contributor {
  display: block;
  font-size: 13px;
  font-weight: bold;

  @media screen and (min-width: $mobile-breakpoint) {
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;
  }
}

.CsoDirectory-img {
  height: 50px;
  max-width: 140px;
  margin-top: 20px;
  background-size: contain;
  background-position: 0 center;
  background-repeat: no-repeat;
}

.CsoDirectory-cover {
  position: absolute;
  top: 0;
  left: 0;
  height: 100%;
  width: 100%;

  .CsoDirectory-item:hover & {
    background: rgba(0, 0, 0, 0.2);
  }
}



